<template>
	<view class="container">
		<uni-forms ref="baseForm" :modelValue="formData">
			<uni-forms-item>
				<uni-easyinput prefixIcon="phone" v-model="formData.mobile" :disabled="isMobile" placeholder="请输入手机号" />
			</uni-forms-item>
			<uni-forms-item class="code-box">
				<uni-easyinput prefixIcon="chat" v-model="formData.code" placeholder="请输入验证码" />
				<button class="count-down" v-if="isShowCountDown">{{countDownNumber}}秒</button>
				<button  @click="sendSms" v-else>获取验证码</button>
			</uni-forms-item>
		</uni-forms>
		<button @click="unbindMobile" v-if="isMobile">解绑手机号</button>
		<button @click="bindMobile" v-else>绑定手机号</button>
	</view>
</template>

<script>
	import {
		mapMutations,mapState
	} from 'vuex'
	export default {
		data() {
			return {
				formData: {
					mobile: '',
					code:'',
					code_confirmation:''
				},
				isMobile:false,
				countDownNumber:60,
				isShowCountDown:false,
				setInterval:''
			}
		},
		onLoad(options) {
			console.log(options.mobile)
			if(options.mobile !== ('undefined'&&'')){
				this.formData.mobile =  options.mobile
				this.isMobile = true
			}
		},
		computed:{
			...mapState(['userInfo'])
		},
		methods: {
			...mapMutations(['setUserInfo']),
			async sendSms() { //发送验证码
			    const response = await this.$api.user.sendSms(this.formData.mobile)
				if(response.data.code == 200){
					this.formData.code_confirmation = response.data.sms
					this.countDown()
					uni.showToast({
						title: response.data.message,
						icon: 'success',
						duration: 2000
					});	
				}
			},
			countDown(){ //倒计时
			    let that = this
				that.isShowCountDown = true
				this.setInterval = setInterval(function(){
					that.countDownNumber--
					if(that.countDownNumber==0){
						that.isShowCountDown = false
						that.formData.code_confirmation = ''
						that.countDownNumber = 60
						clearInterval(this.setInterval)
						return
					}
				},1000)
			},
			async unbindMobile(){ //解绑手机号
			   let  {code,code_confirmation} = this.formData
			   let userInfo  = this.userInfo
			   let data = {
				userId: userInfo.id,
				code: code,
				code_confirmation: code_confirmation,
				isExpires: !this.isShowCountDown
			   }
			   const response = await this.$api.user.unbindMobile(data)
			   if (response.data.code == 200) {
				userInfo.mobile = ''
				this.setUserInfo(userInfo)
				uni.showToast({
					title: response.data.message,
					icon: 'success',
					duration: 2000
				});
				let t = setTimeout(res => {
					uni.navigateBack()
					clearTimeout(t)
				}, 500)
			   }  
			},
			async bindMobile(){ //绑定手机号
				let {mobile,code,code_confirmation} = this.formData
				let userInfo  = this.userInfo
				let data = {
					userId: userInfo.id,
					mobile: mobile,
					code:code,
					code_confirmation:code_confirmation,
					isExpires: !this.isShowCountDown
				}
				const response = await this.$api.user.bindMobile(data)
				if (response.data.code == 200) {
					userInfo.mobile = data.mobile
					this.setUserInfo(userInfo)
					uni.showToast({
						title: response.data.message,
						icon: 'success',
						duration: 2000
					});
					let t = setTimeout(res => {
						uni.navigateBack()
						clearTimeout(t)
					}, 500)
				}
			}
		},
		onHide() {
			clearInterval(this.setInterval)
		}
	}
</script>

<style lang="scss">
	.container {
		background-color: #fff;
		width: 100%;
		height: 100vh;
		padding: 80upx 50upx 0;

		/deep/.is-input-border {
			border-color: rgb(229, 229, 229) !important;
		}

		/deep/.uniui-clear {
			color: rgb(192, 196, 204) !important;
		}

		/deep/.uni-easyinput__content-input {
			height: 70upx;
		}
		

		button {
			background-color: #009000;
			color: #fff;

		}
		
		/deep/.uni-forms-item__content{
			display: flex;
			button{
				margin-left: 20upx;
				height: 75upx;
				line-height: 75upx;
				font-size: 28upx;
				width: 200upx;
			}
		}
		button{
			background-color: #009000;
			color: #fff;
		}
		.count-down{
			background-color: #c7c7c7 !important;
		}
	}
</style>